import { ApiError } from "types/error.ts";
import { isAxiosError } from "axios";
import { isApiErrorResponse } from "./isApiError.ts";

export function handleError(error: unknown): ApiError {
  if (isApiErrorResponse(error)) {
    const strapiError = error.response.data.error;
    return {
      code: String(strapiError.status || error.response.status),
      message:
        strapiError.message || "     ",
      field: strapiError.details?.target,
      originalError: error,
    };
  }

  if (isAxiosError(error)) {
    if (error.code) {
      return {
        code: error.code,
        message:
          error.message || " .    .",
        originalError: error,
      };
    }

    return {
      code: String(error.response?.status || "UNKNOWN"),
      message: error.message || "     ",
      originalError: error,
    };
  }

  if (error instanceof Error) {
    return {
      code: "JS_ERROR",
      message: error.message || "   ",
      originalError: error,
    };
  }

  return {
    code: "UNKNOWN",
    message: "  ",
    originalError: error,
  };
}
